import { request } from 'utils';

const prefix = '/api/categories';

export const getSchool = ({
  id,
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/School',
    method: 'GET',
    params: { id, label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



