import { request } from 'utils';

const prefix = '/api/categories';

export const getEnrollStudyFields = ({
  trainTypeId,
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/EnrollStudyField',
    method: 'GET',
    params: {
      label,
      trainTypeId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const deleteEnrollStudyField = id =>
  request({
    prefix,
    url: '/EnrollStudyField',
    method: 'DELETE',
    params: {},
    data: { id },
  });

export const createEnrollStudyField = ({ id, label, trainTypeId } = {}) =>
  request({
    prefix,
    url: '/EnrollStudyField',
    method: 'POST',
    params: {},
    data: { id, label, trainTypeId },
  });

export const updateEnrollStudyField = ({ id, label, trainTypeId } = {}) =>
  request({
    prefix,
    url: '/EnrollStudyField',
    method: 'PUT',
    params: {},
    data: { id, label, trainTypeId },
  });

export const getEnrollStudyFieldById = id =>
  request({
    prefix,
    url: '/EnrollStudyField/GetById',
    method: 'GET',
    params: { id },
    data: {},
  });



