import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { SelectGender, SelectAjax, InputYear, InputScore10 } from 'components/common';
import { Row, Col, Card, Form, Input } from 'antd';
import { getCandidateById, updateCandidate, createCandidate } from 'services/studentCandidate';
import { requiredRule } from 'utils/validateRules';
import { getAreas } from 'services/area';
import { getListTown } from 'services/town';
import { getEnrollSolutions } from 'services/enrollSolution';
import InputGroupDate from 'components/common/input/InputGroupDate';
import { getTrainTypes } from 'services/trainType';
import { getDistricts } from 'services/district';
import { getSchool } from 'services/school';
import { getEnrollStudyFields } from 'services/enrollStudyField';
import { getQueryParams } from 'utils/urlUtil';

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 5 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 },
  },
  colon: false,
};
const initialValueDefault = {
  id: '',
  trainTypeId: '',
  facultyId: '',
  courseId: '',
  registerSessionId: '',
  semesterId: '',
  yearEduId: '',
  minCreditNormal: '',
  maxCreditNormal: '',
  replayCreditNormal: '',
  isCheckMinNormal: '',
  isCheckMaxNormal: '',
  minCreditLow: '',
  maxCreditLow: '',
  replayCreditLow: '',
  isCheckMinLow: '',
  isCheckMaxLow: '',
  startDate: '',
  endDate: '',
  startDate2: '',
  endDate2: '',
};

@Form.create({ name: 'CandidateStudentFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
  };

  componentDidMount = async () => {
    const { form } = this.props;
    const { id: queryParamId } = getQueryParams();

    if (queryParamId) {
      this.setState({ dataId: queryParamId });
      const initialValue = await this.getDataById(queryParamId);
      form.setFieldsValue(initialValue);
    } else {
      form.setFieldsValue(initialValueDefault);
    }
  };

  getDataById = async id => {
    const res = await getCandidateById({ id });
    return { ...initialValueDefault, ...res.data };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createCandidate(values);
      } else {
        await updateCandidate({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    
    const townAddress = getFieldValue('townAddress');
    const trainTypeId = getFieldValue('trainTypeId');
    return (
      <Card title="Thm ng vin">
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Row>
            <Col span={12}>
              <Form.Item label="H" hasFeedback>
                {getFieldDecorator('firstname', {
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Tn" hasFeedback>
                {getFieldDecorator('lastname', {
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="SBD" hasFeedback>
                {getFieldDecorator('code', {
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Ni sinh" hasFeedback>
                {getFieldDecorator('pobId', {
                  rules: [requiredRule],
                })(<SelectAjax service={getListTown} />)}
              </Form.Item>
              <Form.Item label="Tnh T.Tr" hasFeedback>
                {getFieldDecorator('townAddress', {
                  rules: [requiredRule],
                })(
                  <SelectAjax
                    setFieldsValue={setFieldsValue}
                    service={getListTown}
                  />
                )}
              </Form.Item>
              <Form.Item label="Huyn T.Tr" hasFeedback>
                {getFieldDecorator('permanentAddress', {
                  rules: [requiredRule],
                })(
                  <SelectAjax
                    setFieldsValue={setFieldsValue}
                    service={getDistricts}
                    params={{ townId: townAddress }}
                    allowGetData={!!townAddress}
                  />
                )}
              </Form.Item>
              <Form.Item label="Ngy sinh" hasFeedback>
                {getFieldDecorator('dob', {
                  rules: [requiredRule],
                })(<InputGroupDate />)}
              </Form.Item>
              <Form.Item label="Gii tnh" hasFeedback>
                {getFieldDecorator('gender', {
                  rules: [requiredRule],
                })(<SelectGender />)}
              </Form.Item>

              <Form.Item label="M trng" hasFeedback>
                {getFieldDecorator('schoolId', {
                  rules: [requiredRule],
                })(<SelectAjax service={getSchool} />)}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item label="Phng n TS" hasFeedback>
                {getFieldDecorator('esId', {
                  rules: [requiredRule],
                })(<SelectAjax service={getEnrollSolutions} />)}
              </Form.Item>
              <Form.Item label="Khu vc" hasFeedback>
                {getFieldDecorator('areaId', {
                  rules: [requiredRule],
                })(<SelectAjax service={getAreas} />)}
              </Form.Item>
              <Form.Item label="Nm TN" hasFeedback>
                {getFieldDecorator('graduatedYear', {
                  rules: [requiredRule],
                })(<InputYear min={1000} max={new Date().getFullYear()} />)}
              </Form.Item>
              <Form.Item label="H o to" hasFeedback>
                {getFieldDecorator('trainTypeId', {
                  rules: [requiredRule],
                })(
                  <SelectAjax service={getTrainTypes} />
                )}
              </Form.Item>
              <Form.Item label="Ngnh" hasFeedback>
                {getFieldDecorator('studyField', {
                  rules: [requiredRule],
                })(
                  <SelectAjax 
                    setFieldsValue={setFieldsValue}
                    service={getEnrollStudyFields} 
                    params={{ trainTypeId }}
                    allowGetData={!!trainTypeId}
                  />
                )}
              </Form.Item>
              <Form.Item label="Khi" hasFeedback>
                {getFieldDecorator('testGroup', {
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Mn 1" hasFeedback>
                {getFieldDecorator('gradeM1', {
                  rules: [requiredRule],
                })(<InputScore10 />)}
              </Form.Item>
              <Form.Item label="Mn 2" hasFeedback>
                {getFieldDecorator('gradeM2', {
                  rules: [requiredRule],
                })(<InputScore10 />)}
              </Form.Item>
              <Form.Item label="Mn 3" hasFeedback>
                {getFieldDecorator('gradeM3', {
                  rules: [requiredRule],
                })(<InputScore10 />)}
              </Form.Item>
            </Col>
          </Row>
          <div style={{ textAlign: 'center' }}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={() => this.props.history.goBack()} />
          </div>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



